import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn1 from '../assets/nintendo.jpg';

const Nintendo = () => {
    return (
        <div>
            <h1 className="consolast">Nintendo Switch</h1>
            <img className="consolasi" src={imgn1} alt="playgames" />
            <p className="consolasd">
                Esta consola se diferencia de todas las demás debido a su diseño
                híbrido que permite usarla de tres maneras distintas. Se pueden
                contactar a una televisión al igual que cualquier consola, pero
                lo que la hace única es que se transforma en un dispositivo
                portátil que se puede llevar por todos lados.

                En cuanto a juegos, ofrece títulos clásicos, más  allá de los juegos
                nuevos que van surgiendo como Super Mario Odyssey y The Legend of Zelda:
                Breath of the Wild y Animal Crossing: New Horizons. Además, hay varios
                juegos independientes que son compatibles con esta consola.
            </p>
        </div>
    )

}
export default Nintendo;