import React from 'react';
import '../App.css';
import { Form, Button, Row, Card, Image, Nav, ButtonGroup } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import img from '../assets/404.jpg';
export function PageNotFound() {
    return (
        <div>
            <h1 className="pgnot">
                la pagina no se encuentra... vuelve a inicio.

                <Card.Img className="pgnotf" variant="top" src={img} />
            </h1>
        </div>
    )
}

export default PageNotFound;


