import React from 'react'
import '../App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Navbar, Nav, Form, FormControl, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Home } from './Home';
import PageNotFound from './PageNotFound';
import Top from './Top';
import Retro from './Retro';

const Menu = () => {
    return (
        <Router>
            <Navbar className="menus" bg="dark" variant="dark">
                <Navbar.Brand className="menu" href="/">Inicio</Navbar.Brand>
                <Nav className="mr-justyf">
                    <Nav.Link href="./Top/">top 2021</Nav.Link>
                    <Nav.Link target="_blank" href="https://store.steampowered.com/stats/?l=spanish">mas jugados de steam</Nav.Link>
                    <Nav.Link href="./Retro/">juegos retros</Nav.Link>
                </Nav>
            </Navbar>
            <Switch>
                <Route path="/" exact component={Home} />
                <Route path="/Top" exact component={Top} />
                <Route path="/Retro" exact component={Retro} />
                <Route component={PageNotFound} />
            </Switch>
        </Router>
    )
}

export default Menu;