import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn1 from '../assets/Cyber Shadow.jpg';

const Noticia1 = () => {
    return (
        <div>
            <img className="n2" src={imgn1} alt="playgames" />
            <p className="nd2">
                Fecha: 26 de enero por 19,99 euros
                Lo bueno de Cyber Shadow es que hay ninjas.
                Ninjas robóticos. Ese ya podría ser argumento
                suficiente para justificar su compra, pero es
                que además nos ha encandilado con su estética de 8
                bits y su recuerdo a la saga Ninja Gaiden.
            </p>
        </div>
    )

}
export default Noticia1;