import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn1 from '../assets/play5.jpg';

const Play5 = () => {
    return (
        <div>
            <h1 className="consolast">PlayStation 5</h1>
            <img className="consolasi" src={imgn1} alt="playgames" />
            <p className="consolasd">
                Se trata de la consola más esperada del último tiempo. A diferencia de su antecesora, cuenta con un diseño exclusivo que la hace única, nuevos controles que mejoran la experiencia de juego y acceso a videojuegos exclusivos. Además es compatible con la PS4 lo que permite continuar disfrutando de todos los juegos con los que ya se contaba.
                Viene con un reproductor de Blu-ray 4K que permite disfrutar de una calidad de imagen única hasta el momento.
                Es una gran opción si no se tiene ninguna consola, pero si ya se cuenta con la PS4 o la PS4 Pro quizás los cambios más significativos solamente se pueden disfrutar con una pantalla 4K.
            </p>
        </div>
    )

}
export default Play5;