import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn2 from '../assets/StrongholdWarlords.jpg';

const Noticia3 = () => {
    return (
        <div>
            <img className="n3" src={imgn2} alt="playgames" />
            <p className="nd3">
                Fecha: 9 de marzo por 34,99 euros
                La estrategia siempre ha tenido un papel relevante
                en la plataforma de PC. Por ello Stronghold Warlords
                es la presentación de la longeva franquicia de Firefly
                Studios en territorio asiático. Recuerda: cada movimiento
                puede suponer tu victoria o tu derrota.
            </p>
        </div>
    )

}
export default Noticia3;