import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Nav, Form, FormControl, Button, Card } from 'react-bootstrap';
import imgn1 from '../assets/top2021.jpg';
import imgn2 from '../assets/hitman.jpg';
import imgn3 from '../assets/card2.jpg';
import imgn4 from '../assets/card3.jpg';



const Top = () => {
    return (
        <div className="topf">
            <p className="topt">TOP MAS JUGADOS PARA EL 2021</p>
            <p className="topdesc">Vamos a preparar nuestras carteras para todo lo que se avecina este 2021 para PC. Como siempre, auténticos juegazos se pelean por entrar en nuestra biblioteca y copar los catálogos de la comunidad.
                La lista estará en constante actualización, así que recuerda pasarte de vez en cuando para echarle un vistazo a ese juego del que te habías olvidado o a conocer alguna fecha nueva.</p>
            <img className="topimg" src={imgn1} alt="playgames" />
            <h1 className="titulotop">top 3 2021</h1>
            <Card className="cardtop" style={{ width: '18rem' }}>
                <Card.Img className="tamañoimg" variant="top" src={imgn2} />
                <Card.Body>
                    <Card.Title>Hitman 3</Card.Title>
                    <Card.Text>

                        Hitman 3 llegó a todas las plataformas
                        para continuar una saga tan elegante como
                        entretenida. Espionaje, pistolas y sigilo
                        y esta en las dos generaciones.

                    </Card.Text>
                
                </Card.Body>
            </Card>
            <Card className="cardtop2" style={{ width: '18rem' }}>
                <Card.Img className="tamañoimg" variant="top" src={imgn3} />
                <Card.Body>
                    <Card.Title>Little Nightmares 2</Card.Title>
                    <Card.Text>
                        Nos mantenemos en el peculiar género del miedo,
                        pero con un minúsculo protagonista y unos siniestras
                        gráficos que evocan al relato de Coraline, del director
                        Henry Selick.
                    </Card.Text>
                   
                </Card.Body>
            </Card>
            <Card className="cardtop3" style={{ width: '18rem' }}>
                <Card.Img className="tamañoimg" variant="top" src={imgn4} />
                <Card.Body>
                    <Card.Title>It Takes Two</Card.Title>
                    <Card.Text>
                        Takes Two, que requiere de dos usuarios  le ha abierto muchas puertas desde el punto de vista promocional, pero son sus puzles y gráficos los que han terminado por convencer a la audiencia.
                    </Card.Text>
                  
                </Card.Body>
            </Card>


        </div >
    )

}
export default Top;