import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn1 from '../assets/hitman.jpg';

const Noticia1 = () => {
    return (
        <div>
            <hr />
            <img className="n1" src={imgn1} alt="playgames" />
            <p className="nd1">
                El cierre de la trilogía. Hitman 3 es el juego que nos desvelará
                el destino final de el Agente 47, a través de una serie de localizaciones
                de todo el mundo. En ellas, tendremos que realizar asesinatos y encargos
                de las formas más imaginativas que se nos ocurran.
            </p>
        </div>
    )

}
export default Noticia1;