import React from 'react'
import '../App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Table, ListGroup } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Retro = () => {
    return (
        <div className="bgr">
            <h1 className="titulor"> JUEGOS RETRO</h1>
            <p className="desr">
                La nostalgia por el arcade recorre las venas de
                todos los que un día pasaron sus veranos colando
                monedas de 25 pesetas en máquinas recreativas.
                En este verano de supuesta vuelta a la normalidad,
                nosotros queremos hacer un viaje hacia el pasado pensado
                para amantes de los videojuegos y de la tecnología que
                dejamos atrás, pero que siempre vuelve. Estas son nuestras
                recomendaciones para tu verano retro-gamer, que te hacemos
                con la colaboración de nuestro equipo y de Javier Gayo,
                coordinador del Grado en Diseño de Productos Interactivos de U-tad:</p>
            <Table striped bordered hover variant="dark">

                <thead>
                    <tr>
                        <th>#</th>
                        <th>Nombre</th>
                        <th>Fecha de lanzamiento</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1</td>
                        <td>Shenmue</td>
                        <td>24 de noviembre de 2000</td>
                    </tr>
                    <tr>
                        <td>2</td>
                        <td>Super Mario World 2: Yoshi's Island</td>
                        <td>Sin confirmar</td>
                    </tr>
                    <tr>
                        <td>3</td>
                        <td>Devil May Cry</td>
                        <td>7 de diciembre de 2001</td>

                    </tr>
                    <tr>
                        <td>4</td>
                        <td>Super Mario Kart</td>
                        <td>21 de enero de 1993</td>
                    </tr>
                    <tr>
                        <td>5</td>
                        <td>Shenmue II</td>
                        <td> 30 de noviembre de 2001</td>
                    </tr>
                    <tr>
                        <td>6</td>
                        <td>Super Metroid</td>
                        <td>19 de marzo de 1994</td>
                    </tr>
                    <tr>
                        <td>7</td>
                        <td>Fatal Fury 3</td>
                        <td>: Sin confirmar</td>
                    </tr>
                    <tr>
                        <td>8</td>
                        <td>Turok: Dinosaur Hunter</td>
                        <td>4 de marzo de 1997</td>
                    </tr>
                    <tr>
                        <td>9</td>
                        <td>Stunt Race FX</td>
                        <td>27 de octubre de 1994</td>
                    </tr>
                    <tr>
                        <td>10</td>
                        <td>Samurai Shodown II</td>
                        <td>Sin confirmar</td>
                    </tr>
                </tbody>
            </Table>
        </div>
    )

}
export default Retro;
