import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import imgn1 from '../assets/xboxjpg.jpg';

const Xbox = () => {
    return (
        <div>
            <h1 className="consolast">Xbox Series X</h1>
            <img className="consolasi" src={imgn1} alt="playgames" />
            <p className="consolasd">
                Esta es una consola súper potente,
                pensada para los fervientes jugadores. Cuenta con 16 GB de
                memoria GDDR6, soporte para frecuencia de actualización variable
                (VRR), sombreado de frecuencia variable (VRS) y un modo de baja
                latencia para una mejor respuesta.

                En cuanto a los juegos, es compatible con los títulos de Xbox anteriores.
                Además, ofrece opciones exclusivas que mantienen el interés de los usuarios.
            </p>
        </div>
    )

}
export default Xbox;