import React from 'react'
import '../App.css';
import { Carousel, CarouselItem, CarouselCaption } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Imagen from '../assets/marioo.jpg'
import Imagen2 from '../assets/cratos.jpg'
import Imagen3 from '../assets/minecratf.jpg'

const Carrusel = () => {
    return (
        <div>
            <p className="descripcionT">A principios de los años 90 las videoconsolas dieron
                un importante salto técnico gracias a la competición
                de la llamada "generación de 16 bits" compuesta por la
                Mega Drive, la Super Nintendo Entertainmet de Nintendo,
                la PC Engine de NEC, conocida como Turbografx en occidente
                y la CPS Changer de (Capcom).
                Junto a ellas también apareció la Neo Geo (SNK)
                una consola que igualaba las prestaciones técnicas de un
                arcade pero demasiado cara para llegar de forma masiva a los hogares.
                Esta generación supuso un importante aumento en la cantidad de jugadores
                y la introducción de tecnologías como el CD-ROM, una importante evolución
                dentro de los diferentes géneros de videojuegos, principalmente gracias a
                las nuevas capacidades técnicas.</p>
            <br /><br />
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={Imagen}
                        alt="First slide"
                    />
                    <Carousel.Caption>
                        <h3 className="ctitulo">SUPER MARIO</h3>
                        <p className="cdescripcion">un viejo confiable</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={Imagen2}
                        alt="Second slide"
                    />

                    <Carousel.Caption>
                        <h3 className="ctituloc">god of war</h3>
                        <p className="cdescripcionc">aventuras como nunca.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={Imagen3}
                        alt="Third slide"
                    />

                    <Carousel.Caption>
                        <h3 className="ctitulom">MINECRAFT</h3>
                        <p className="cdescripcionm">crea tu propio mundo sin limites</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
    )
}
export default Carrusel;